<div class="custom-container tip"><p class="custom-container-title">TIP</p>
<p>在<code>docs</code>目录中，除了<code>docs/.vuepress</code>文件夹外的文件及文件夹，都是博客文章，<code>docs/.vuepress</code>目录不能删除，里面存放临时文件，build命令生成的文件，以及静态文件，主题配置文件</p>
</div>
<p>例如上面的<code>style.md</code>和<code>comment/readme.md</code>文件都是文章，都可以直接通过<code>http://localhost:8080/style.html</code>和<code>http://localhost:8080/comment/index.html</code>进行访问(8080端口不一定和我的一样)</p>
